@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap);
ul{padding:0;list-style:none}.App{margin:0 auto;max-width:1200px;padding:20px;font-family:Roboto, sans-serif;font-size:28px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.button{margin:20px;padding:10px 20px;background-color:#018b01;color:white;font-size:20px;font-weight:600;border:3px solid transparent;border-radius:10px;text-transform:capitalize}.button--red{background-color:red}.button--blue{background-color:#4c4cf1}.button:hover{box-shadow:0 0 8px 0 #000}.button:focus{outline:none;border-color:#5f5e5e}.goods-list__item{margin:10px;padding:15px 50px;text-align:center;background:#b1b1b1;color:white;font-weight:600;border-radius:10px}

